body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none;
}

.Container {
  max-width: 600px;
  padding: 0 1rem;
}
.Event{
    position: relative;
    padding: 1rem;
    background-color: #fff;
    border: 1px solid black;
    border-radius: 4px;
  }
  .Event-Favorite{
    display: block;
    position: absolute;
    top: 5%;
    right: 2%;
    font-size: 2rem;
    line-height: 2rem;
    cursor: pointer;
  }
  .Event-Favorite:after {
    width: 100%;
    height: 100%;
  }
  .Event-Favorite--true:after {
    content: '\2605';
  }
  .Event-Favorite--false:after {
    content: '\2606';
  }
#info{
  position: absolute;
  top: 0;
  left: 0;
  /* width: 100%; 
  height: 100%; 
  pointer-events: none; */
}
.Info {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%; 
  height: 100%; 
}
.Info-Wrapper {
  box-sizing: border-box;
  padding-top: 1rem;
  position: relative;
  top: 50%;
  left: 50%;
  background-color: white;
  width: 100%;
  max-width: 600px;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  border: 1px solid black;
}
.Info-Title {
  margin-top: 0;
}
@media screen and(min-width:600px){
  .Info {
    min-height: 400px
  }
}
.MainEvents-Refresh {
  display: block;
  margin: 1rem auto;
  padding: 1rem;
  background-color: #ccc;
  font-weight: 600;
}
.Filters {
  margin: 1rem auto;
}
.Filters-Label{
  margin-right: 1rem;
}
.Filters-Select {
  width: 50%;
}
.Filters-Row {
  margin-bottom: 1rem;
}
.Tabs {
    padding: 1rem 1rem 0 1rem;
    margin-bottom: 10px;
    border-bottom: 1px solid black;
    box-sizing: border-box;
  }
  .Tabs-Button{
    color: black;
    font-weight: 600;
    display: inline-block;
    padding: 1rem;
    border: 1px solid black;
    border-bottom: none;  
    border-left: none;
  }
  .Tabs-Button--selected{
    background-color: #ccc;
  }
  .Tabs-Button:last-child{
    border-top-right-radius: 6px;  
  }
  .Tabs-Button:first-child{
    border-left: 1px solid black;
    border-top-left-radius: 6px;  
  }
